body{
  background-color: black;
}

span{
  background-color: green;
}

input{
  width: 50vw;
}

h1{
  font-size: 1em;
}

h4{
  font-size: .4em;
}

.container{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-row-gap: 5vh;
  margin-top: 5vh;
  align-items: center;
  justify-items: center;
}

.card-container{
  width: 60px;
  height: 60px;
  perspective: 1000px;
}

.front-card, .back-card{
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.card-flip{
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

img{
  width: 60px;
  height: 60px;
}

.App {
  color: #fff;
  text-align: center;
}

.card-container:hover .card-flip {
  transform: rotateY(180deg);
}

.back-card{
  transform: rotateY(180deg);
  background-color: darkblue;
}

.back-card h5{
  font-size: .3em;
  margin: 0;
}

.back-card h4{
  margin: 0;
  padding: 0;
}

#info{
  font-size: .8em;
}

@media(min-width: 320px){
  .card-container{
